<template>
      <section class="product-page spad">
       <div class="container">
           <div class="row">
               <div class="col-lg-9  order-lg-2">
                   <div class="row">
                     <div v-bind:key="VoitureElem.id" v-for="VoitureElem in Voitures" class="col-lg-3 col-md-6 col-sm-6">
                     <Voiture :id="VoitureElem.id" :Nom="VoitureElem.nom" :Marque="VoitureElem.marque.nom" :Image="VoitureElem.thumbnail" />
                     </div>
                </div>
                <div class="row">
                   <div class="col-lg-6 col-md-6">
              
                         <div class="product__pag">
                           <a @click="ChangePage" v-bind:class="{ active_pag : pagNumber==this.Page }"  v-for="pagNumber in PagesArray" :key="pagNumber" href="#">{{ pagNumber }}</a>
                       </div>
                   </div>
                   <div class="col-lg-6 col-md-6">
                       <div class="product__show">
                        <p>Showing 1–6 of 8 results</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-lg-3 order-lg-1 col-md-6">
            <div class="product__sidebar">
                <div class="product__sidebar__search">
                    <form action="#">
                        <input type="text" @change="Searching()" @keyup="Searching()" v-model="SearchValue" placeholder="Chercher...">
                        <button type="submit"><i class="fa fa-search"></i></button>
                    </form>
                </div>
                <div class="product__sidebar__categories">
                    <h4>Siege</h4>

                      <Matiere @SendMatiere="SendMatiere" :num="index" :part="'Siege'" v-on:AddMatiere="AddMatiere(1)" v-on:RemoveMatiere="RemoveMatiere" v-for="index in MatiereNumbers.Siege" :key="index"/>

                    <br>
                    <br>
                    <h4>Panneaux</h4>
                      <Matiere @SendMatiere="SendMatiere" :num="index" :part="'Panneaux'" v-on:AddMatiere="AddMatiere(2)"  v-on:RemoveMatiere="RemoveMatiere" v-for="index in MatiereNumbers.Panneaux" :key="index"/>

        
        <br>
                    <br>
                    <h4>Tableaux</h4>
                    
                      <Matiere @SendMatiere="SendMatiere" :num="index" :part="'Tableaux'" v-on:AddMatiere="AddMatiere(3)"  v-on:RemoveMatiere="RemoveMatiere" v-for="index in MatiereNumbers.Tableaux" :key="index"/>
        <br>
                    <br>
                    <h4>Volant</h4>
                    
                      <Matiere @SendMatiere="SendMatiere" :num="index" :part="'Volant'" v-on:AddMatiere="AddMatiere(4)"  v-on:RemoveMatiere="RemoveMatiere" v-for="index in MatiereNumbers.Volant" :key="index"/>
                              <br>
                    <br>
                    <h4>Couture</h4>
                    <div class="divmatiere">
                
  <div class="color-wrapper"  >
  <div class="color-holder call-picker" ></div>
  <div class="color-picker"  style="display: none" @click="ChangeColorCouture">
  </div>
</div>
</div>

<button class="btnicon" @click="EnableCouture">
<i class="fa fa-plus" ></i>
</button>
                </div>
            </div>
                              </div>

    </div>
</div>
</section>
</template>



<script>
import Voiture from './Voiture.vue'
import Matiere from './Matiere.vue'
export default {
    name :'ListVoitures',
    data(){
        return{
            Voitures : Array,
            Marque:'',
            SearchValue : '',
            Page : Number,
            PagesNumber : Number,
            PagesArray : [],
            MatiereNumbers : {
                Siege :1,
                Panneaux : 1,
                Tableaux : 1,
                Volant : 1
            },
            MatiereDetail : {
                Siege: [],
                Panneaux: [],
                Tableaux: [],
                Volant: [],
                Couture: ''
            }
        }
    },
    components:{
        Voiture,Matiere
    },
    methods:{
    EnableCouture(event){    
     var button = event.target
        var itag = event.target

             if(button.tagName==="I"){
             button = button.parentElement
             }else {
                itag = event.target.children[0]
             }
     var  Couture =  button.previousSibling

        if (this.MatiereDetail.Couture===''){
                    Couture.style.opacity=1;
        Couture.style.pointerEvents =  "auto";
            this.MatiereDetail.Couture='#000000'
                   itag.classList.remove("fa-plus");
        itag.classList.add("fa-close");
        }else {
                        this.MatiereDetail.Couture=''

             Couture.style.opacity=0.2;
     Couture.style.pointerEvents =  "none";
     itag.classList.remove("fa-close");
     itag.classList.add("fa-plus");
        }
        this.fetchWithMatieres()

    }
    ,
    ChangeColorCouture(event){
                var codeHex = event.target.dataset.hex
        if (typeof codeHex != 'undefined') {
      this.MatiereDetail.Couture = codeHex
        }
            this.fetchWithMatieres()

    },
    SendMatiere(num,part,matiere){
        if (part==='Siege'){
            console.log(num)
            if (this.MatiereDetail.Siege.length==num-1){
                this.MatiereDetail.Siege.push(matiere)
            }else if (this.MatiereDetail.Siege.length>=num){
                this.MatiereDetail.Siege[num-1] = matiere
            }
            console.log("siege",this.MatiereDetail.Siege)
        }else if (part==='Panneaux'){
       if (this.MatiereDetail.Panneaux.length==num-1){
                this.MatiereDetail.Panneaux.push(matiere)
            }else if (this.MatiereDetail.Panneaux.length>=num){
                this.MatiereDetail.Panneaux[num-1] = matiere
            }
            console.log(this.MatiereDetail.Panneaux)
                    }else if (part === 'Tableaux'){
        if (this.MatiereDetail.Tableaux.length==num-1){
                this.MatiereDetail.Tableaux.push(matiere)
            }else if (this.MatiereDetail.Tableaux.length>=num){
                this.MatiereDetail.Tableaux[num-1] = matiere
            }
            console.log(this.MatiereDetail.Tableaux)
                    }else if (part=='Volant'){
         if (this.MatiereDetail.Volant.length==num-1){
                this.MatiereDetail.Volant.push(matiere)
            }else if (this.MatiereDetail.Volant.length>=num){
                this.MatiereDetail.Volant[num-1] = matiere
            }
            console.log(this.MatiereDetail.Volant)
                    }
    this.fetchWithMatieres()
    },

    AddMatiere(num){
        if (num===1){
        if (this.MatiereNumbers.Siege<2)
        this.MatiereNumbers.Siege+=1;
        }else if (num===2){
        if (this.MatiereNumbers.Panneaux<2){
        this.MatiereNumbers.Panneaux+=1;
        } 
        }else if (num===3){
        if (this.MatiereNumbers.Tableaux<2)
        this.MatiereNumbers.Tableaux+=1; 
        }else {
        if (this.MatiereNumbers.Volant<2)
        this.MatiereNumbers.Volant+=1;
        }
        },

    RemoveMatiere(num,part){
        if (part==='Siege'){
        if (this.MatiereNumbers.Siege>1){
        this.MatiereDetail.Siege.splice(num-1,1)
        this.MatiereNumbers.Siege-=1;
        }
        }else if (part==='Panneaux'){
        if (this.MatiereNumbers.Panneaux>1){
        this.MatiereDetail.Panneaux.splice(num-1,1)
        this.MatiereNumbers.Panneaux-=1; 
        }
        }else if (part==='Tableaux'){
        if (this.MatiereNumbers.Tableaux>1){
        this.MatiereDetail.Tableaux.splice(num-1,1)
        this.MatiereNumbers.Tableaux-=1; 
        }
        }else if (part==='Volant'){
        if (this.MatiereNumbers.Volant>1){
        this.MatiereDetail.Volant.splice(num-1,1)
        this.MatiereNumbers.Volant-=1;
        }
        }
    this.fetchWithMatieres()

    },
    SetPagesArray(){
        this.PagesArray=[]
        var start = 1
        var end = 7

        if (this.PagesNumber<7){
            end = this.PagesNumber
        }

        if (this.Page>1 && 7<this.PagesNumber){
            start = this.Page-1
            if (this.PagesNumber>this.Page+5){
                end = this.Page+5
            }else {
                end = this.PagesNumber
            }
        }
        for (var i=start;i<=end;i++){
            this.PagesArray.push(i)
        }
        
    },
    ChangePage(event){
        event.preventDefault()
        this.Page = parseInt(event.target.innerHTML)
         this.fetchWithMatieres()
    },
     Searching(){
         this.Marque=''
        this.fetchWithMatieres()
    },
    async fetchWithMatieres(){
       let cookie = document.cookie
        let csrfToken = cookie.substring(cookie.indexOf('=') + 1)
        const res = await fetch('http://0.0.0.0:5000/voitures/', {
        method: 'POST',
        headers: {
          'Content-type': 'application/json',
          'X-CSRFToken': csrfToken
        },
        body:  
            JSON.stringify({details :this.MatiereDetail,
            page : this.Page,
            nom : this.SearchValue.toUpperCase(),
            marque : this.Marque,
            })
            ,
      })
   const data = await res.json()
   console.log('data' ,data)
        this.Voitures = data.data
        console.log('voiture',this.Voitures);
        this.PagesNumber = Math.ceil(data.count/8)
        this.SetPagesArray()
    },
    },
     created(){
var url_string = window.location.href; 
    var url = new URL(url_string);
    var cat = url.searchParams.get("cat");  
    this.Marque = cat ;
    console.log(this.Marque);
          this.Page = 1
         this.fetchWithMatieres()
    }
}
</script>

<style >
.divmatiere_2 {
    background-color: gray;
    opacity: 0.2;
    position:relative;
    top: 0;
    left: 0;
    pointer-events: auto
}
.active_pag {
    background: #1b2839;
    border-color: #1b2839;
    color: #ffffff !important;;
}
</style>