
<template>
<div class="divmatiere">
                      <div class="nice-select input__filter" tabindex="0">
                          <span class="current">Vrai Cuir</span><ul class="list"><li data-value="" class="option selected" @click="ChangeMatiereCuir">Vrai Cuir</li><li data-value="" class="option" @click="ChangeMatiereCuir">Simili-Cuir</li></ul></div>
  <div class="color-wrapper"  >
  <div class="color-holder call-picker" ></div>
  <div class="color-picker"  style="display: none" @click="ChangeColor">
  </div>
</div>
</div>

<button class="btnicon" @click="AddMatiere">
<i class="fa fa-plus" ></i>
</button>
   <br>
                    <hr>
</template>

<script>
var elements = document.getElementsByClassName("color-item");
var myFunction = function() {

}
for (var i = 0; i < elements.length; i++) {
    elements[i].addEventListener('click', myFunction, false);
}
export default {
    nom:'Matiere',
    props:{
        num : Number,
        part : String
    },
    data(){
        return {
            Active : false,
            codeHex : '#000000',
            Cuir : 'Vrai Cuir'
        }
    },
    emits: ["AddMatiere","RemoveMatiere","SendMatiere"],
    methods:{
    ChangeColor(event){
        var codeHex = event.target.dataset.hex
        if (typeof codeHex != 'undefined') {
      this.codeHex = codeHex
      this.$emit('SendMatiere',this.num,this.part,{color:this.codeHex,cuir:this.Cuir});


        }
       
    },
    ChangeMatiereCuir(event){
        this.Cuir = event.target.innerHTML
        this.$emit('SendMatiere',this.num,this.part,{color:this.codeHex,cuir:this.Cuir});
    },
    AddMatiere(event){
        var button = event.target
        var itag = event.target

             if(button.tagName==="I"){
             button = button.parentElement
             }else {
                itag = event.target.children[0]
             }
     var  matiere =  button.previousSibling

        this.Active = !this.Active
        if (this.Active){
        matiere.style.opacity=1;
        matiere.style.pointerEvents =  "auto";
        itag.classList.remove("fa-plus");
        itag.classList.add("fa-close");
        this.$emit('AddMatiere');
        this.$emit('SendMatiere',this.num,this.part,{color:this.codeHex,cuir:this.Cuir});


        }else {
                 matiere.style.opacity=0.2;
     matiere.style.pointerEvents =  "none";
     itag.classList.remove("fa-close");
     itag.classList.add("fa-plus");
              this.$emit('RemoveMatiere',this.num,this.part);

        }

    }
    },
}
</script>

<style >
.divmatiere {
    background-color: gray;
    opacity: 0.2;
    position:relative;
    top: 0;
    left: 0;
    pointer-events: none
}
 .btnicon {
  background-color: white; /* Blue background */
  border: none; /* Remove borders */
  color: gray	; /* White text */
  padding: 1px 3px; /* Some padding */
  font-size: 19px; /* Set a font size */  		
  box-shadow: 0px 0px 3px 1px gray;
  margin-top: 11px;
  margin-left: 15px;

}
</style>